import React, { Component } from "react";
import Products from "./components/products";

class BarleySpring extends Component {
	constructor() {
		super();
		this.name = "barley_spring";
		this.seeds  = [
			{ name: 'Паула', img: 'paula'}
		];
	}
	render() {
		return (
			<div className="content">
				<div className="product-title">
					<h2>Ячмінь Ярий</h2>
					<div className="product-subtitle">
						Це найбільш широко розповсюджений вид пшениці, вирощується у Європі, Азії та Індії, Північній та Південній Америці і Австралії.
					</div>
				</div>
				<Products name={this.name} seeds={this.seeds} />
			</div>
		);
	}
}

export default BarleySpring;


